export default {
	data() {
		return {
			uname:'',
			code:'',
			upwd:'',
			repassword:'',
			EXphoto:'',
			count: '',
			timer: null,
			show: true,
			disabled:false,
			read:false
		};
	},
	methods:{
		register(){
			if(this.uname == ''){
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(!(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/.test(this.uname))){
				uni.showToast({
					title: '手机号码格式不正确',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.code == ''){
				uni.showToast({
					title: '请输入验证码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.upwd == ''){
				uni.showToast({
					title: '请输入密码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.upwd != this.repassword ){
				uni.showToast({
					title: '两次输入的密码不一致',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.EXphoto == ''){
				uni.showToast({
					title: '请输入邀请人',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(!(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/.test(this.EXphoto))){
				uni.showToast({
					title: '邀请人手机号格式不正确',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			uni.hideKeyboard()
			this.disabled = true
			this.$store.dispatch('signup',{
				uname: this.uname,
				upwd: this.upwd,
				repassword:this.$attrs,
				code:this.code,
				EXphoto:this.EXphoto
			}).then(res=>{
				this.disabled = false
				uni.showToast({
					title: '注册成功',
					icon: 'none', //loading   none  success
					success: (res)=>{
						setTimeout(function(){
							uni.reLaunch({
								url:'login'
							});																	
						},1500)
					}
				});
			}).catch(err=>{
				this.disabled = false
			})
		},
		get_code(){
			this.$store.dispatch('signupCode',{
				mobile:this.uname
			}).then(res=>{
				// 等待时长60s获取一次验证码
				const TIME_COUNT = 60;
				if (!this.timer) {
					this.count = TIME_COUNT;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			}).catch(err=>{})
		},
		//注册协议
		agreement(){
			uni.navigateTo({
				url:'agreement'
			})
		},
		//选择协议
		read_xieyi(){
			this.read = !this.read
		}
	},
	onLoad() {
		
	},
}
